<template>
  <div class="querySportScore"v-loading="loadingStatus">
    <div class="callBackBtn">
      <el-button @click="callBackPages" type="primary" icon="arrow-left">返回</el-button>
    </div>
    <div class="gradeWrap">
      <div>考试名称：<p>{{examName}}</p></div>
      <div>年级：<p>{{gradeName}}</p></div>
      <div>班级：<p>{{className}}</p></div>
    </div>
    <div class="editBtn" :class="{hideStatus: !hideBtnState}">
      <el-button @click="editPages"  type="info">编辑</el-button>
    </div>
    <div class="hideBtn" :class="{hideStatus: hideBtnState}">
      <el-button @click="cancelPages" type="warning">取消</el-button>
      <el-button @click="savePages" type="success">保存</el-button>
    </div>
    <div class="scoreTable">

      <table id="table">
        <tr v-for="tableTh in tableData.tableHeader">
          <th v-for="thData in tableTh" :rowspan="thData.rowNum" :colspan="thData.colNum">{{thData.text}}</th>
        </tr>
        <tr ref="tr" v-for="tableTrBody in tableData.tableBody">
          <td v-for="(tdValue, tdKey) in tableTrBody" v-show="tdKey != 'studentId' ">
            <!--<input ref="input" maxlength="4" @change="queryChangeStatus($event)" :class="editStatus ? 'allowEdit' : 'notAllowEdit'" disabled="disabled" :model="tdKey" :value="tdValue"  v-if="">-->
            <input ref="input" maxlength="4" @change="queryChangeStatus($event,tdKey)" :class="editStatus ? 'allowEdit' : 'notAllowEdit'" disabled="disabled" :model="tdKey" :value="tdValue"   v-if="">
          </td>
        </tr>
      </table>
    </div>
    <div class="countWrap">
      <div class="headcount">合计：{{count}}</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'querySportScore',
    data() {
      return {
        examName:"",
        examId:"",
        gradeName:"",
        className:"",
        schoolYear:"",
        classId:"",
        term:"",
        count:"",
        tableData:[],
        oldData: [],
        hideBtnState:true,
        editStatus: false,
        queryChangeCount:0,
        errorMsg:0,
        loadingStatus:true,
        inputValue:''
      }
    },
    mounted(){
//        页面渲染请求
      let $this = this;
      var scoreId = this.$route.query;
      $this.examId = scoreId.examId;
      $this.classId = scoreId.classId;
//      this.$ajax('static/tableScore.json',
      this.$ajax('physical/getClassScore',
          scoreId
      )
        .then(function (response) {
//             声明初始化变量
          $this.tableData = response.data.tableData;
          $this.oldData = response.data.tableData;
          $this.examName = response.data.examName;
          $this.gradeName = response.data.gradeName;
          $this.className = response.data.className;
          $this.schoolYear = response.data.schoolYear;
          $this.term = response.data.term;
          $this.count = response.data.count;
          $this.loadingStatus = false;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    methods: {
      callBackPages(){
//          返回上一页
        this.$router.go(-1);
      },
      editPages(){
//          编辑表格
        this.$confirm('您将编辑当前的表格, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var input = this.$refs.input;
          input.forEach( ( item, i ) => {
            var key = item.getAttribute("model");
            //判断是否可编辑项
            if (key == "studentName" || key == "sid"){
              item.disabled = "disabled"
            }else {
              item.disabled = false;
            }
          });
          this.editStatus = true;
          this.hideBtnState = false;
//          this.$message({
//            type: 'success',
//            message: '删除成功!'
//          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消操作'
          });
        });
//        console.log(this.$refs);

      },
      cancelPages(){
        //取消编辑
        var oldData = this.tableData;
        this.$set(this.tableData,oldData);
        var input = this.$refs.input;
        input.forEach( ( item, i ) => {
          item.disabled = "disabled";
          item.style.border = ""
        });
        this.editStatus = false;
        this.hideBtnState = true;
        this.errorMsg = 0;

      },
      savePages(){
//          保存提交数据
        const $this = this;
        if (this.queryChangeCount == 0){
          this.editStatus = false;
          this.hideBtnState = true;
          var input = this.$refs.input;
          input.forEach( ( item, i ) => {
            item.disabled = "disabled";
          });
          return;
        }else {
//          if (this.errorMsg > 0){
//            this.$alert('您输入修改的成绩格式有误, 一共有'+this.errorMsg+'处，请您修改后从新提交', '提示', {
//              confirmButtonText: '确定',
//              callback: action => {
////                  this.$message({
////                    type: 'info',
////                    message: `action: ${ action }`
////                  });
//              }
//            });
//          }else{
            this.$confirm('您将保存当前已编辑过的成绩表格, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              var tdList = this.$refs.tr;
              var userData = [];
              var user = {};
              tdList.forEach( ( item, i ) => {
                var tdBox = item.querySelectorAll('td input');
                user = new Object();
                (function (i) {
                  tdBox.forEach( ( value, j ) => {
                    value.disabled= "disabled";
                    var key = value.getAttribute("model");
                    var $value = value.value;
                    user[key] = $value;
                  });
                })(i);
                userData.push(user);
              });
              userData = JSON.stringify(userData);
              this.$ajax('physical/updateClassScore',
                {
                  "examId":$this.examId,
                  "classId":$this.examId,
                  "data":userData
                }
              )
                .then(function (response) {
//                console.log(response);
                  $this.$message({
                    type: 'success',
                    message: '保存成功!'
                  });
                })
                .catch(function (error) {
//                console.log(error);
                  $this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
                });
              userData = JSON.parse(userData);
              this.oldData.tableBody = userData;
              this.queryChangeCount = 0;
              this.editStatus = false;
              this.hideBtnState = true;
            }).catch(() => {
              $this.$message({
                type: 'info',
                message: '已取消操作'
              });
            });
          }
//        }
      },
      queryChangeStatus(event,tdKey){
//          输入数据监测、验证

//          console.log(tdKey,event);
//          console.log(this.$refs.input);
//          var input = this.$refs.input;
//          input.forEach( ( item, i ) => {
////              var studentid = item.getAttribute("studentid");
//            if (studentid == studentId){
//                console.log(item.value)
//            }
//          });
//        var scoreReg = /^(0|\d{1,2}|100)(\.\d)?$/;//0-100 可以带一位小数
        this.queryChangeCount += 1;
//        var scoreValue = event.target.value;
//        var res = scoreReg.test(scoreValue);
////        console.log(scoreValue,res,event);
//        if (!res){
//          event.target.style.border = "1px solid red";
//          this.errorMsg += 1;
//        } else {
//          event.target.style.border = "";
//          this.errorMsg -= 1;
//        }
      }
    }
  }
</script>
<style>
  .querySportScore{
    padding: 30px;
  }
  .callBackBtn {
    text-align: left;
  }
  .editBtn {
    margin: 20px 0;
    text-align: right;
    padding-right: 30px;
  }
  .hideBtn {
    margin: 20px 0;
    text-align: right;
  }
  .hideStatus {
    display: none;
  }
  .scoreTable #table {
    width: 100%;
    border: 1px solid #dfe6ec;
    margin-top: 20px;
  }
  #table tr,td,th {
    border-bottom: 1px solid #dfe6ec;
    border-right: 1px solid #dfe6ec;
  }
  #table th {
    padding: 1% 0.5%;
    font-size: 14px;
    font-weight: 600;
    text-align: center;
    background-color: #eef1f6;
  }
  #table td {
    box-sizing: border-box;
    height: 30px;
  }
  #table td:nth-child(1) {
    width: 80px;
  }
  #table td:nth-child(2) {
    width: 150px;
  }
  #table input {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    font-size: 16px;
    text-align: center;
  }
  /*.errorInput {*/
  /*outline: 1px solid red;*/
  /*}*/
  .allowEdit {
    box-sizing: border-box;
    /*border: none;*/
    outline: none;
    border: 1px solid #0C9EE9;
  }
  .notAllowEdit{
    border: none;
    outline: none;
  }
  .tdName {
    width: 20%;
  }
  .tdNumber {
    width: 30%;
  }
  #table input:disabled{
    /*border:1px solid #DDD;*/
    /*background-color: white;*/
    /*color:#ACA899;*/
  }
  .countWrap {
    margin: 20px 0;
    text-align: left;
  }
  .headcount {
    display: inline-block;
    text-align: center;
    width: 90px;
    height: 35px;
    line-height: 35px;
    border-radius: 5px;
    background: #eef1f6;
    font-size: 15px;
    font-weight: 600;
  }
  .gradeWrap {
    margin-top: 20px;
  }
  .gradeWrap > div{
    margin: 10px 0;
    font-weight: 600;
  }
  .gradeWrap > div > p {
    display: inline-block;
    margin-left: 20px;
  }
</style>



